.progress-circle {
  position: relative;
  display: inline-block;
  margin: 1rem;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #ebebeb;
  &:after {
    content: '';
    display: inline-block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-animation: colorload 2s;
    animation: colorload 2s; }
  span {
    font-size: 1.7rem;
    color: #8b8b8b;
    position: absolute;
    left: 33%;
    top: 33%;
    display: block;
    width: 100px;
    height: 100px;
    line-height: 90px;
    margin-left: -30px;
    margin-top: -30px;
    padding-left: 10px;
    text-align: center;
    border-radius: 50%;
    background: #fff;
    z-index: 1;
    &:after {
      content: "%";
      font-weight: 600;
      color: #8b8b8b; } }
  &.progress-1:after {
    background-image: linear-gradient(54deg, #30bae7 50%, transparent 50%, transparent), linear-gradient(270deg, #30bae7 50%, #ebebeb 50%, #ebebeb); }
  &.progress-2:after {
    background-image: linear-gradient(0deg, #d74680 50%, transparent 50%, transparent), linear-gradient(270deg, #d74680 50%, #ebebeb 50%, #ebebeb); }
  &.progress-3:after {
    background-image: linear-gradient(-18deg, #15c7a8 50%, transparent 50%, transparent), linear-gradient(270deg, #15c7a8 50%, #ebebeb 50%, #ebebeb); }
  &.progress-4:after {
    background-image: linear-gradient(36deg, #eb7d4b 50%, transparent 50%, transparent), linear-gradient(270deg, #eb7d4b 50%, #ebebeb 50%, #ebebeb); } }

@-webkit-keyframes colorload {
  0% {
    opacity: 0;
    transform: rotate(0deg); }

  100% {
    opacity: 1;
    transform: rotate(360deg); } }

